.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  z-index: 1;
}

.modal__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 600px;
  height: 600px;
  text-align: center;
  background-color: transparent;
  z-index: 2;
  animation-name: animatetop;
  animation-duration: 0.5s;
  @media (max-width: 768px) {
    width: 280px;
    height: 500px;
  }
}

@keyframes animatetop {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal__cards {
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: $background-beige;
  border: 5px solid $background-beige;
  border-radius: 5px;
}

.modal__close {
  position: absolute;
  right: -15px;
  top: -30px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.modal__projects {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  max-width: 535px;
  margin: 0 auto;
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.modal__projects-item {
  width: 240px;
  padding: 5px;
  background-color: transparent;
  margin: 0 auto;
}

.modal__projects-name {
  font-size: 14px;
  font-weight: 700;
  text-align: start;
  margin: 3px 0;
}

.modal__projects-img {
  @extend %photo-center;
  border: 1px solid $color-text;
  border-radius: 5px;
  width: 240px;
  height: 156px;
}

.modal__projects-description {
  font-size: 14px;
  margin: 5px 0;
  text-align: start;
}

.modal.active {
  display: block;
}
